.home h1{
  color: #ffffffc4;
  font-family:Georgia, 'Times New Roman', Times, serif;
}
.home p{
  background-color: none;
  padding: 1.2rem;
  color: white;
  box-shadow: rgba(0, 0, 0, 0.45) 0px 25px 20px -20px;

}
.project{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  gap: 4.8rem;
}
.flip-card {
  background-color: transparent;
  width: 20vw;
  height: 35vh;
  perspective: 1000px;
}

.flip-card-inner {
  position: relative;
  transition: transform 0.6s;
  transform-style: preserve-3d;
  border-top: 2px  solid white;
  box-shadow: 0 4px 8px 0 rgba(247, 2, 2, 0.808);
}

.flip-card:hover .flip-card-inner {
  transform: rotateY(180deg);

}

.flip-card-front, .flip-card-back {
  position: fixed;
  height:fit-content;
  width: fit-content;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
}

.flip-card-front {
  /* width: inherit; */
  margin: 1rem auto;
  color: black;

}
.flip-card-back {
  color: rgb(0, 0, 0);
  transform: rotateY(180deg);
  justify-content: center;
  padding: 0.2rem;
  display: block;
  margin: 0rem auto;
}
.card-out{
  display: flex;
  gap: 0.5rem;
  justify-content: center;
  align-items: center;
  /* flex-direction: column; */
}

@media only screen and (max-width: 1200px) {
  .project{
    display: flex;
    gap: 10.3rem;
    flex-wrap: wrap;
  }
}
@media only screen and (max-width: 974px) {
  .project{
    display:flex;
    flex-direction: row;
    gap: 4.8rem;
    flex-wrap: wrap;
    padding: 1.2rem;
  }
  .card-out > a{
    display: block;
    margin-top: 15.2rem ;
  }
  .flip-card{
    width: 15.2rem;
  }
 
    .content{
      width: 42rem;
    }
    .container{
      height: 150vh;
    }

}
